import React, { useState } from 'react'
import SingleColor from './SingleColor'

import Values from 'values.js'

function App() {
  const [color, setColor] = useState('');
  const [error, setError] = useState(false);
  const [list, setList] = useState(new Values('#f15251').all(10));

  const handleSubmit = (e)=>{
    e.preventDefault();
  try {
    let colors = new Values(color).all(10);
    setError(false)
    setList(colors)
  } catch (error) {
    setError(true);
    console.log(error);
  }
  }

  return (
    <>
<section className="container">
  <h3>color generator</h3>
  <form onSubmit={handleSubmit}>
    <input type="text" value={color} onChange={(e)=>setColor(e.target.value)} placeholder='#f15251' className={`${error?'error' : null}`} />
    <button type='submit' className="btn">submit</button>
  </form>
</section>


<section className='colors'>
{list.map((item,index)=>{
  
  return <SingleColor key={index} {...item} index={index} hexColor={item.hex}/>
})}
</section>

    </>
  )
}

export default App
